
import Vue from "vue"
import { CripSelectOption } from "../../types/plugin"

export default Vue.extend({
  name: "CripTags",

  props: {
    tags: { type: Array, required: true },
  },

  computed: {
    show(): boolean {
      return this.tags.length > 0
    },
  },

  methods: {
    remove(option: CripSelectOption) {
      this.$emit("remove", option)
    },
  },
})



